import uniCard from '@/components/uni-card/uni-card';
export default {
	components: {
		uniCard
	},
	data() {
		return {
			listData: [],
			user: {},
			token: '',
			typeId: -1,
			lookObj: {},
			isLook: false
		};
	},
	onLoad(e) {
		this.user = JSON.parse(e.user);
		this.token = e.token;
		this.typeId = e.typeId;
		let url = 'manageHabit/selectUserHabits.json';
		const data = {
			typeId: this.typeId,
			teacherId: this.user.id,
			token: this.token
		};
		this.uniHttp.getJSON(url, data, res => {
			if (res.data.success) {
				let data = res.data.data;
				this.listData = data;
				for (let i = 0; i < data.length; i++) {
					if (data[i].type == 0) {
						let imgArr = [];
						if (data[i].workUrl) {
						}
					} else if (data[i].type == 1) {
					} else if (data[i].type == 2) {
						let videoArr = [];
						if (data[i].workUrl) {
							let obj = {
								img: data[i].photoUrl,
								video: data[i].workUrl
							};
							videoArr.push(obj);
						} else {
							videoArr.push('');
						}
						if (data[i].twoWork) {
							let obj = {
								img: data[i].twoPhoto,
								video: data[i].twoWork
							};
							videoArr.push(obj);
						} else {
							videoArr.push('');
						}
						if (data[i].threeWorl) {
							let obj = {
								img: data[i].twoPhoto,
								video: data[i].threeWorl
							};
							videoArr.push(obj);
						} else {
							videoArr.push('');
						}
						data[i].videoArr = videoArr;
					}
				}
			}
		});
	},
	methods: {
		videoErrorCallback() {},
		lookImg(urls, i) {
			// uni.previewImage({
			// 	urls: urls[i].img
			// });
		},
		lookVideo(v) {
			this.isLook = true;
			this.lookObj = v;
		}
	}
};
